* {
  box-sizing: border-box;
  padding:0;
  margin:0;

}

.mypage-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
  background-color: white;
}

/* 버튼 스타일(전역) */
.mypage-btn {
  font-size: 1rem;
  font-weight: bold;
  margin:0.5rem 0 0 0;
  padding: 0.5rem 2rem;
  cursor: grab;
  border:rgb(255, 255, 255);
  border-radius: 0.2rem;
  transition:0.15s; 
  background-color: rgb(255, 255, 255);
  color:black;
}

.mypage-btn:hover {
  background: #e6e7e7;
}

.blackbtn {
  background-color: black;
  color:rgb(255, 255, 255);
}

.blackbtn:hover {
  background: #655b6f;
}

.mypage-btnUnderline {
  font-size: 1rem;
  text-decoration: underline;
  color:rgb(32, 31, 31);
  border: none;
  font-weight: 600;
  background-color: white;
}

button.mypage-btnUnderline {
  cursor: pointer;
  transition:0.1s; 
}
button.mypage-btnUnderline:hover {
  color:rgb(157, 157, 157);
}

.redbtn {
  background-color: rgb(247, 103, 103);
  color:white;
  margin: 0;
}

.graybtn {
  color: rgb(94, 93, 93);
} 

/* mypage-화면상단 */
.mypage-container__top {
  display: flex;
  flex-direction: row;
  width: 600px;
}

/* 프로필 사진 */
.mypage-container__topleft {
  flex-basis: 150px;
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items: center;
  height: 250px;
}

.mypage-container__topleft > img {
  width: 10rem;
  height: 10rem;
  border-radius: 100%;
}

/* 프로필 사진 - 로딩화면 */
.mypage-topleft__skeletonContainer {
  flex-basis: 150px;
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items: center;
  height: 250px;
}

.mypage-topleft__skeletonWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 10rem;
  height: 10rem;
  border-radius: 100%;
  background-color: #d9d6d6;
}
.mypage-topleft__skeletonImg {
  position:relative;
  top:20px; left: 20px;
  border: 8px solid rgb(250, 250, 250);
  border-top:8px solid #0b0b0b;
  border-radius:100%;
  transform:translate(-50%, -50%); 
  width:40px;
  height:40px;
  transition:all .2s;
  animation-name:spinCircle;
  animation-duration:.8s;
  animation-iteration-count:infinite;
  animation-timing-function:linear;
}

@keyframes spinCircle {
  from {
      transform:translate(-50%, -50%) rotate(0);
  }
  to {
      transform:translate(-50%, -50%) rotate(360deg);
  }
}

/* 닉네임, 아이디, 자기소개 */
.mypage-container__topright {
  flex-basis: 450px;
}

.mypage-wrapper__topright {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 1rem 0 0 2rem;
}

.mypage-topright__nickname {
  margin-top:1rem;
}

.mypage-topright__nickname span:nth-of-type(1) {
  font-size: 2.5rem;
  font-weight: bold;
}
.mypage-topright__introduce {
  font-size: 1.2rem;
  font-weight: 500;
  color: rgb(126, 123, 123);
  margin: 0.5rem 0;
  text-align: left;
  max-height: 80px;
}

/* 닉네임 & 자기소개 (콘텐츠 수정화면) */
.mypage-inputForm__topright {
  height: 180px;
  display: flex;
  flex-direction: column;
  justify-content:space-evenly;
  align-items: flex-end;
}

.mypage-inputForm__nickname {
  font-size: 1.5rem;
  color:rgb(82, 79, 79);
  width: 400px;
  height: 40px;
  border: solid rgb(197, 193, 193) 1px;
  border-radius: 3px;
}

.mypage-inputForm__introduce{
  font-size: 1rem;
  color:rgb(82, 79, 79);
  width: 400px;
  height: 30px;
  border: solid rgb(197, 193, 193) 1px;
  border-radius: 3px;
}

/* mypage-화면 하단 */
.mypage-container__bottom {
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
  justify-content:center;
  align-items: center;
}

.mypage-bottom__wrapper {
  display: flex;
  flex-direction: row;
  width: 630px;
}

.mypage-bottomleft__title {
  flex-basis: 150px;
  margin: 1rem;
  font-size: 1rem;
  text-align: left;
}

.mypage-bottomright__content {
  flex-basis: 450px;
  display:flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

/* 주활동지역 */
.mypage-bottomright__content {
  display: flex;
  flex-direction: column;
  /* flex-basis: 450px; */
}

.mypage-bottomright__content-activityArea {
  display: flex;
  width: 450px;
  flex-direction: row;
  border: solid rgb(197, 193, 193) 1px;
  border-radius: 10px;
  align-items: center;
  justify-content:flex-start;
  padding-left: 8px;
  flex-wrap: wrap;
  margin-top: 2px;
}
/* .mypage-bottomright__content-activityArea:focus-within {
  border: 1px solid gray;
} */

ul.mypage-bottomright__activityAreas {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 8px;
}

li.mypage-bottomright__activityArea {
  width: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: white;
  list-style: none;
  background-color: black;
  margin: 0 6px 6px 0;
  border-radius: 6px;
  padding: 6px 6px;
  font-size: 0.8rem;
  font-weight: 700;
}

.mypage-bottomright__activityArea-name{
  background-color: black;
  font-size: 1rem;
}

.mypage-bottomright__activityArea-closeicon{
  margin-left: 6px;
  background-color:#fff;
  color:black;
  border-radius: 100%;
  width: 16px;
  height: 16px;
  line-height: 16px;
  font-size: 0.8rem;
  text-align: center;
  cursor: pointer;
}
.mypage-bottomright__activityAreaInputContainer {
  display: flex;
  width: 450px;
}
.mypage-bottomright__activityAreaInput {
  flex:1;
  border: none;
  font-size: 0.95rem;
  height: 30px;
  margin: 0rem 0.5rem 0.5rem 0;
  color:rgb(82, 79, 79);
  outline: none;
}

.mypage-bottomright__areainput:focus {
  outline: transparent;
}

.mypage-bottomright__areainput:focus-within {
  border: 1px solid (82, 79, 79);
}

/* 주활동지역 - 자동완성 */
.mypage-bottomright__autoCompleteContainer {
  display: flex; 
  flex-direction: row;
  width: 450px;
  align-items: center;
  flex-wrap: wrap; 
  position:absolute;
  border-radius: 5px ;
  top:28rem;
  box-shadow: 0 4px 6px rgb(32 33 36 / 15%);
}

.mypage-bottomright__autoComplete {
  background-color: white;
  color:rgb(82, 79, 79);
  font-weight: 600;
  list-style-type: none;
  margin: 0.2rem 0.2rem;
  padding: 0.3rem;
  border-radius: 3px;
  cursor: pointer;
}

.mypage-bottomright__autoComplete:hover {
  background-color:#c7c7c7;
  transition: 0.5s;
}

/* 내가 쓴 글보기 */
.mypage-bottomright__myPostsClickBtnContainer {
  display: flex;
}

.mypage-bottomright__myPostsCloseBtnWrapper {
  display: flex;
  width: 400px;
  justify-content: flex-end;
  margin-top: 0.5rem;
}

.mypage-bottomright__myPostsContainer {
  margin-top: 0.6rem;
  display: flex;
  flex-direction: column;
}

.mypage-bottomright__myPostsWrapper {
  display: flex;
  flex-direction: column;
  flex-basis: 30px;
  align-items: flex-start;
}
  
.mypage-bottomright__myPost {
  text-decoration: none;
  color: rgb(43, 43, 43);
  padding-top: 0.5rem;
  cursor: pointer;
  font-weight: 500;
}


/* 비밀번호 변경 */
.mypage-bottomright__passwordForm {
  display: flex;
  flex-direction: column;
}

.mypage-bottomright__pwFormContainer {
  display: flex;
  flex-direction: column;
  padding-top: 1rem;
}

.mypage-bottomright__pwFormWrapper{
  display: flex;
  flex-direction:column;
  list-style: none;
}

.mypage-bottomright__pwFormWrapper p {
  color:gray;
  text-align: left;
  font-size: 0.9rem;
}

.mypage-bottomright__pwDescription {
  font-size: 1rem;
  color:rgb(82, 80, 80);
  text-align: left;
  margin-top: 0.2rem;
}
.mypage-bottomright__pwInput {
  font-size: 1.2rem;
  border: rgb(166, 163, 163) solid 1px;
  border-radius: 3px;
  width:400px;
  padding:0.1rem 0.2rem;
}

.mypage-bottomright__pwInput:focus {
  outline: transparent;
}

.mypage-bottomright__buttonwrapper {
  display: flex;
  flex-direction: row;
  justify-content: right;
}

/* 회원정보 삭제 */
.mypage-bottomright__deleteAccountWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.mypage-bottomright__deleteAccountWrapper > button {
  margin: 0.7rem 0;
  padding: 0.5rem 1.2rem;
}
.mypage-bottomright__deleteAccountDescription {
  font-size: 0.9rem;
  color:rgb(129, 126, 126);
}

.mypage-bottomright__ModalBackground {
  position: fixed;
  width:100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
}

.mypage-bottomright__ModalView {
  height: 12rem;
  width: 20rem;
  padding: 2rem;
  border-radius: 10px;
  background-color: white;
}

.mypage-bottomright__ModalView > h3 {
  margin-bottom: 1rem;
  text-align: left;
}

.mypage-bottomright__ModalMsg {
  margin-bottom: 1rem;
  text-align: left;
}

.mypage-bottomright__ModalButtonArea {
  display: flex;
  flex-direction: row;
  justify-content:space-around;
  align-items: center;
  margin-top: 1.5rem;
}